body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.container{width:1200px;margin:0 auto}.container980{width:980px;margin:0 auto}.hidden_textfield{overflow:hidden;width:0.1px;height:0.1px;opacity:0;position:absolute;z-index:-1}.contact{display:flex;align-items:center;letter-spacing:0.2em;border-top:1px solid #E3E3E3}.contact:last-child{border-bottom:1px solid #E3E3E3}.contact h4{text-transform:capitalize;width:200px;padding:10px;padding-right:none;font-size:14px;color:#737373;font-weight:normal;font-family:"Open Sans",sans-serif;border-right:1px solid #E3E3E3;margin-block-start:0em;margin-block-end:0em}.userLink{padding:0px 10px;color:#222222}.userLink:hover{transition:all .2s linear;color:#4dcadd}.userLink:hover{text-decoration:underline}
